import React from 'react';
import { Link, Route, Switch } from 'react-router-dom';
import blogdata from '../../data/blogdata.json';
import { useParams } from 'react-router-dom';
import { useRouteMatch } from 'react-router-dom';
import BlogGrid from '../blog-grid';



const BlogGridPostLIst = () =>{

  
  const imgattr = 'image'
  const publicUrl = process.env.PUBLIC_URL+'/'
  const data = blogdata.blogs;
  const { path } = useRouteMatch();
  const { url } = useParams();

    return (
   <section className="blog-3-area blog-page">
     <div className="container">
      <div className='row'>
        <div className='col-lg-12 blog-item '>
          <div className='mt-30'>
            <div className='blog-thumb'>
              <img src="../assets/images/service-details-thumb.jpg" alt=""/>
            </div>
            <div className='blog-content'>
              <span>IT Support Blog </span>
              <h3 className='title'>Welcome to our IT Support Blog</h3>
              <p className='mt-3'>The requirement for businesses to have technical experts on staff has become widespread as the digital age progresses further and farther into its second decade. It is now more than just tech-based firms' job to maintain their IT infrastructure. Because of the always-connected and always-on nature of modern business, It Support has become an indispensable component without which most businesses would come to a grinding halt.</p>
              <p className='mt-3'>IT failures can happen without alert, and when they do, organisations are often left struggling to recognise underlying causes, restore vital systems, and troubleshoot and resolve any resulting problems. Regrettably, IT maintenance and monitoring have yet to keep pace with the dependence on IT generally. When the ever-evolving risks posed by cyberattacks and security breaches are factored in, the It Support fragility of modern businesses becomes even more evident. To resolve this significant gap in the market, we at IT Support are determined to address all users' problems and leave you with top-quality IT service.</p>
              <p className='mt-3'>Our services include supporting and facilitating your AOL Mail enquiries which include recovering your lost password, helping you to access your lost account, AOL Desktop Gold Support, supporting your QuickBooks installation process, assisting you in your QuickBooks journey, helping you recover your various email accounts, providing you with hardware maintenance service, onsite support and many other techs related support services. Using our support services, you are guaranteed to feel satisfied with our exceptional service quality. Feel free to explore all our support services.</p>
            </div>
          </div>
        </div>
      </div>
       <div className="row justify-content-center">
        { data.map( ( item, i ) =>
           <div key={ i } className="col-lg-4 col-md-7 col-sm-9">
             <div className="blog-item mt-80">
               <div className="blog-thumb">
                 <img src={ publicUrl+item.image } alt={ imgattr } />
                  <Link to={ item.url }>{ item.cat }</Link>
               </div>
               <div className="blog-content borex-go-top" key={item.url}>
                  <Link to={`../blog${item.url}`} ><h4 className="title">{ item.title }</h4></Link>
                 <p className='discription'>{ item.discription}</p>
                 <ul>
                    <li><Link to={ item.url }><i className="fa fa-calendar" />{ item.date }</Link></li>
                    <li><Link to={ item.url }><i className="fa fa-user-o" /> BY { item.username }</Link></li>
                 </ul>
               </div>
             </div>
           </div>
         ) }

       </div>
     </div>
   </section>


    );
}

export default BlogGridPostLIst;
