import React, { Component } from 'react';
import sectiondata from '../../data/sections.json';
import parse from 'html-react-parser';
import { Link } from 'react-router-dom';

class SubPart extends Component {

    render() {

        let publicUrl = process.env.PUBLIC_URL+'/'
        let data = sectiondata.subpart

    return <section className="sub-area">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-lg-3 col-md-6 col-sm-9">
                <div className="sub-thumb mt-30">
                  <img src={ publicUrl+data.image } alt={ data.alt } title={ data.title } />
                </div>
              </div>
              { data.items.map( ( item, i )=>
                <div key={ i } className="col-lg-3 col-md-6 col-sm-9">
                  <div className="sub-item white-bg mt-30 borex-go-top">
                    <h5 className="title"  dangerouslySetInnerHTML={{__html: item.title}}></h5>
                    <i className={ item.icon } />
                    <Link to={ item.url }><i className="flaticon-right-arrow-1" /></Link>
                  </div>
                </div>
               ) }

            </div>
          </div>
        </section>

        }
}

export default SubPart