                      <p>Downloading attachments, which is mostly what this step is for, is how you clear off storage in your AOL inbox. When you save attachments, you ensure that you still have access to them even if something happens to your AOL Mail 295 account. On the other hand, it's not just the attachments that may be saved offline; emails can also be saved.</p>
