import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import widgetdata from '../../data/widgetdata.json';

class Sidebar extends Component {
  render() {
   let imgattr = 'image'
   let publicUrl = process.env.PUBLIC_URL+'/'
   let data = widgetdata.latestposts

    return (
      <div className="col-lg-4 col-md-6 col-sm-8">
         <div className="blog-sidebar">
           <div className="blog-search-item box mt-30">
             <div className="blog-sidebar-title">
               <h4 className="title">Search</h4>
             </div>
             <div className="blog-search">
               <form action="#">
                 <div className="input-box">
                   <input type="text" placeholder="Search..." />
                   <button><i className="fa fa-search" /></button>
                 </div>
               </form>
             </div>
           </div>
           <div className="blog-post-item box mt-40">
             <div className="blog-sidebar-title">
               <h4 className="title">{ data.title }</h4>
             </div>
             <div className="blog-post">
              { data.items.map( ( item, i ) =>
                 <div key={ i } className="item">
                   <img src={ publicUrl+item.image } alt={ imgattr } />
                   <h5 className="title"><Link to="/blog">{ item.title }</Link></h5>
                   <ul>
                     <li><i className="fa fa-calendar" /> { item.date }</li>
                     <li><i className="fa fa-eye" /> { item.view }</li>
                     <li><i className="fa fa-comment-o" /> { item.comment }</li>
                   </ul>
                 </div>
                ) }

             </div>
           </div>
           <div className="blog-tag-item box mt-40">
             <div className="blog-sidebar-title">
               <h4 className="title">Popular Tags</h4>
             </div>
             <div className="blog-tag">
               <ul>
                 <li><Link to="/blog">Aol</Link></li>
                 <li><Link to="/blog">Email</Link></li>
                 <li><Link to="/blog">Aol email 295</Link></li>
               </ul>
               <ul>
                 <li><Link to="/blog">AOL Mail</Link></li>
                 <li><Link to="/blog">Aol mail help</Link></li>
                 <li><Link to="/blog">Aol</Link></li>
               </ul>
               <ul>
                 <li><Link to="/blog">QuickBooks</Link></li>
                 <li><Link to="/blog">Mail</Link></li>
                 <li><Link to="/blog">Accounting</Link></li>
               </ul>
               <ul>
                 <li><Link to="/blog">Hardware</Link></li>
                 <li><Link to="/blog">Software</Link></li>
                 <li><Link to="/blog">Support</Link></li>
               </ul>
               <ul>
                 <li><Link to="/blog">Network</Link></li>
                 <li><Link to="/blog">Email</Link></li>
                 <li><Link to="/blog">Cloud</Link></li>
               </ul>
             </div>
           </div>
           <div className="blog-categories-item box mt-40">
             <div className="blog-sidebar-title">
               <h4 className="title">{ widgetdata.categorywidget.title }</h4>
             </div>
             <div className="categories-item">
               <ul>
                { widgetdata.categorywidget.items.map( ( item, i )=> 
                 <li key={ i }><Link to={ item.url }><i className="fa fa-angle-double-right" /> { item.cat }   ( { item.number } )</Link></li>
                 ) }
    
               </ul>
             </div>
           </div>
           <div className="blog-instagram-item box mt-40">
             <div className="blog-sidebar-title">
               <h4 className="title">{ widgetdata.instagram.title }</h4>
             </div>
             <div className="instagram-item">
               <ul>
                 { widgetdata.instagram.firstlist.map( ( item, i )=> 
                   <li key={ i }><a href="#"><img src={ publicUrl+item.image_url } alt={ imgattr } /></a></li>
                 ) }
               </ul>
               <ul>
                  { widgetdata.instagram.secenditem.map( ( item, i )=> 
                   <li key={ i }><a href="#"><img src={ publicUrl+item.image_url } alt={ imgattr } /></a></li>
                 ) }
               </ul>
             </div>
           </div>
           <div className="blog-social-item box mt-40">
             <div className="blog-sidebar-title">
               <h4 className="title">{ widgetdata.socialicon.title }</h4>
             </div>
             <div className="social-item">
               <ul>
                 { widgetdata.socialicon.socialicon.map( ( item, i )=> 
                   <li key={ i } ><a href={ item.url }><i className={ item.icon } /></a></li>
                 ) }
               </ul>
             </div>
           </div>
         </div>
       </div>

    );
  }
}

export default Sidebar;
