import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, BrowserRouter as Router, HashRouter, Route, Switch } from "react-router-dom";
import { createBrowserHistory } from "history";
import blogdata from './data/blogdata.json';
import Singleblogdata from './data/single-blogdata.json';
import HomeV1 from './components/home-v1';
import ServiceV1 from './components/hardware-support';
import ServiceV2 from './components/software-support';
import ServiceV3 from './components/network-support';
import ServiceV4 from './components/cloud-support';
import ServiceV5 from './components/helpdesk-support';
import ServiceV6 from './components/quickbooks-support';
import ServiceV7 from './components/email-support';
import ServiceV8 from './components/aol-mail-login-support';
import HardwareDetails from './components/hardware-maintenance-and-support-services';
import SoftwareDetails from './components/best-software-support-services';
import NetworkDetails from './components/best-network-support-services';
import CloudDetails from './components/best-cloud-services';
import HelpdeskDetails from './components/helpdesk-support-services';
import QuickbooksDetails from './components/quickbooks-support-services';
import EmailDetails from './components/email-support-services';
import faq from './components/faq';
import BlogGrid from './components/blog-grid';
import Aol295 from './components/aol-mail-295'; 
import AolemailErrror from './components/aol-mail-295-aol-email-login-error-support'; 
import DataEntry from './components/automating-data-entry-quickbooks-online'; 
import MailIphone from './components/aol-mail-not-working-on-iphone'; 
import MailDownload from './components/aol-mail-295-download'; 
import RecoverMail from './components/recover-aol-forgotten-mail-password'; 
import RecoverAolmail from './components/how-to-recover-aol-mail'; 
import QuickbooksSoftware from './components/quickbooks-2020-software'; 
import MailWindows from './components/login-to-aol-mail-in-windows-10'; 
import QuickbookOnline from './components/intuit-quickbook-online-version'; 
import ManagingAol from './components/setting-up-and-managing-an-aol-mail-account'; 
import CreateAolmail from './components/how-can-you-create-aol-mail-account'; 
import IncreaseAol from './components/increase-productivity-results-with-aol-mail'; 
import OpenQuickbooks from './components/what-does-negative-open-balance-mean-in-quickbooks'; 
import AolmailComputer from './components/how-to-download-aol-mail-to-computer'; 
import HowQuickbooks from './components/what-is-quickbooks-and-how-to-use-quickbooks-intuit'; 
import AolmailNotworking from './components/why-aol-mail-295-is-not-working-on-iPhone-nd-how-to-solve-it'; 
import PerformAccount from './components/how-to-perform-account-reconciliations'; 
import EvolutionMail from './components/the-evolution-of-aol-mail-295'; 
import RepairMail from './components/how-to-repair-aol-mail-295-not-receiving-email'; 
import CreateInvoice from './components/how-to-use-quickbooks-to-create-invoices'; 
import Contact from './components/contact';
import MailLogin from './components/mail-login';
import AolGold from './components/aol-desktop-gold-download';
import DesktopGold from './components/aol-desktop-gold';    
import OnlineSupport from './components/onsite-support-near-me';
import BlogGridPostLIst from './components/blog-components/blog-grid-post-list';

const customHistory = createBrowserHistory();

class Root extends Component {
    render() {
        return(
            <BrowserRouter history={customHistory} basename="/">
                <Router >
                    <div>
                    <Switch>
                        <Route exact path="/" component={HomeV1} />
                        <Route path="/hardware-support" component={ServiceV1} />
                        <Route path="/software-support" component={ServiceV2} />
                        <Route path="/network-support" component={ServiceV3} />
                        <Route path="/cloud-support" component={ServiceV4} />
                        <Route path="/helpdesk-support" component={ServiceV5} />
                        <Route path="/quickbooks-support" component={ServiceV6} />
                        <Route path="/email-support" component={ServiceV7} />
                        <Route path="/aol-mail-login-support" component={ServiceV8} />
                        <Route path="/faq" component={faq} />
                        <Route exact path="/blog" component={BlogGrid} />
                        <Route path="/contact-us" component={Contact} />
                        <Route path="/hardware-maintenance-and-support-services" component={HardwareDetails} />
                        <Route path="/best-software-support-services" component={SoftwareDetails} />
                        <Route path="/best-network-support-services" component={NetworkDetails} />
                        <Route path="/best-cloud-services" component={CloudDetails} />
                        <Route path="/helpdesk-support-services" component={HelpdeskDetails} />
                        <Route path="/quickbooks-support-services" component={QuickbooksDetails} />
                        <Route path="/email-support-services" component={EmailDetails} />
                        <Route path="/mail-login" component={MailLogin} />
                        <Route path="/aol-desktop-gold-download" component={AolGold} />
                        <Route path="/aol-desktop-gold" component={DesktopGold} />
                        <Route path="/onsite-support-near-me" component={OnlineSupport} />
                        <Route path="/blog/:aol-mail-295" component={Aol295} />
                        <Route path="/blog/:aol-mail-295-aol-email-login-error-support" component={AolemailErrror} />
                        <Route path="/blog/:automating-data-entry-quickbooks-online" component={DataEntry} />
                        <Route path="/blog/:aol-mail-not-working-on-iphone" component={MailIphone} />
                        <Route path="/blog/:aol-mail-295-download" component={MailDownload} />
                        <Route path="/blog/:recover-aol-forgotten-mail-password" component={RecoverMail} />
                        <Route path="/blog/:how-to-recover-aol-mail" component={RecoverAolmail} />
                        <Route path="/blog/:quickbooks-2020-software" component={QuickbooksSoftware} />
                        <Route path="/blog/:login-to-aol-mail-in-windows-10" component={MailWindows} />
                        <Route path="/blog/:intuit-quickbook-online-version" component={QuickbookOnline} />
                        <Route path="/blog/:setting-up-and-managing-an-aol-mail-account" component={ManagingAol} />
                        <Route path="/blog/:how-can-you-create-aol-mail-account" component={CreateAolmail} />
                        <Route path="/blog/:increase-productivity-results-with-aol-mail" component={IncreaseAol} />
                        <Route path="/blog/:what-does-negative-open-balance-mean-in-quickbooks" component={OpenQuickbooks} />
                        <Route path="/blog/:how-to-download-aol-mail-to-computer" component={AolmailComputer} />
                        <Route path="/blog/:what-is-quickbooks-and-how-to-use-quickbooks-intuit" component={HowQuickbooks} />
                        <Route path="/blog/:why-aol-mail-295-is-not-working-on-iPhone-nd-how-to-solve-it" component={AolmailNotworking} />
                        <Route path="/blog/:how-to-perform-account-reconciliations-using-the-online-version-of-quickbooks" component={PerformAccount} />
                        <Route path="/blog/:the-evolution-of-aol-mail-295" component={EvolutionMail} />
                        <Route path="/blog/:how-to-repair-aol-mail-295-not-receiving-email" component={RepairMail} />
                        <Route path="/blog/:how-to-use-quickbooks-to-create-invoices" component={CreateInvoice} />

                        <Route exact path="/blog/:url" component={BlogGridPostLIst} />
                    </Switch>
                    </div>
                </Router>
            </BrowserRouter>
        )
    }
}

export default Root;

ReactDOM.render(<Root />, document.getElementById('borex'));
