import React, { Component } from 'react';
import sectiondata from '../../data/sections.json';
import parse from 'html-react-parser';
import { Link } from 'react-router-dom';


class LatestNews extends Component {

    render() {

        let publicUrl = process.env.PUBLIC_URL+'/'
        let imagealt = 'image'
        let data = sectiondata.latestNews

    return <section className="blog-area pt-115 pb-120">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-lg-12">
                <div className="section-title section-title-2 text-center">
                  <span>{ data.title }</span>
                  <h3 className="title">{ data.subtitle } </h3>
                </div>
              </div>
            </div>
            <div className="row justify-content-center">
            { data.items.map( ( item, i ) =>
               <div key={ i } className="col-lg-6 col-md-12 col-sm-9">
                    <div className="blog-item mt-30 wow fadeInLeft animated" data-wow-duration="1500ms" data-wow-delay={ item.delay } >
                      <div className="row align-items-center justify-content-center">
                        <div className="col-lg-7 col-md-7 order-2 order-md-1">
                          <div className="blog-content borex-go-top">
                            <span>{ item.meta }</span>
                            <h4 className="title "><Link to={ item.url }>{ item.title }</Link></h4>
                            <p>{ item.content } </p>
                            <Link to={ item.url }><i className="flaticon-right-arrow-1" /> VIEW MORE</Link>
                          </div>
                        </div>
                        <div className="col-lg-5 col-md-5 order-1 order-md-2">
                          <div className="blog-thumb">
                            <img src={ publicUrl+item.image } alt={ imagealt } />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
            ) }

            </div>
          </div>
        </section>
        }
}

export default LatestNews