import React, { Component } from 'react';
import sectiondata from '../../data/sections.json';
import parse from 'html-react-parser';
import { Link } from 'react-router-dom';

class WhyChooseUs extends Component {

    render() {

        let publicUrl = process.env.PUBLIC_URL+'/'
        let imagealt = 'image'
        let data = sectiondata.whychooseus
        let customclass = this.props.customclass ? this.props.customclass : ''

    return <section className={"why-choose-area "+customclass}>
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6 col-md-9">
              <div className="why-choose-content">
                <div className="content">
                  <span>{ data.title }</span>
                  <h3 className="title">{ data.subtitle }</h3>
                  <p>{ data.content }</p>
                </div>
                <div className="choose-list d-block d-sm-flex">
                 { data.items.map( ( item, i )=>
                    <div key={ i } className="item">
                        <i className={ item.icon } />
                        <h6 className="title">{ item.title }</h6>
                        <p>{ item.content }</p>
                      </div>
                  ) }
                  
                </div>
                <ul className="borex-go-top">
                  <li><Link className="main-btn" to="/aol-mail-support">View more <i className="flaticon-right-arrow" /></Link></li>
                  <li><Link to="/aol-mail-support"><i className="flaticon-layers" /> more features </Link></li>
                </ul>
              </div>
            </div>
            <div className="col-lg-6">
              <div className="why-choose-thumb">
                <img src="assets/images/why-choose-thumb.jpg" alt="AOL Mail 295" title="AOL Mail 295" />
              </div>
            </div>
          </div>
        </div>
      </section>


        }
}

export default WhyChooseUs