import React, { Component } from 'react';
import sectiondata from '../../data/sections.json';
import parse from 'html-react-parser';
import { Link } from 'react-router-dom';

class NetworkDetails extends Component {

    render() {

        let publicUrl = process.env.PUBLIC_URL+'/'
        let imagealt = 'image'
        let data = sectiondata.servicev7

    return <section className="services-2-area">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-lg-8">
            <div className="section-title section-title-3 text-center">
              <span>{ data.title }</span>
              <h3 className="title">{ data.subtitle }</h3>
              <p className='my-3'>{data.subsubtitle}</p>
            </div>
          </div>
        </div>
        <div className="row justify-content-center">
        { data.items.map( ( item, i )=>
            <div key={ i } className="col-lg-4 col-md-6 col-sm-8">
              <div className="services-item mt-30 wow fadeInUp animated borex-go-top" data-wow-duration="1000ms" data-wow-delay={ item.delay }>
                <i className={ item.icon }/>
                <h5 className="title">{ item.title }</h5>
                <p>{ item.content }</p>
                <Link className="main-btn" to={ item.url }>Upgrade your account</Link>
              </div>
            </div>
         ) }
          

        </div>
      </div>
    </section>


        }
}

export default NetworkDetails