import React, { Component } from 'react';
import sectiondata from '../../data/sections.json';
import parse from 'html-react-parser';
import { Link } from 'react-router-dom';


class ServicesV6 extends Component {

    render() {

        let publicUrl = process.env.PUBLIC_URL+'/'
        let imagealt = 'image'
        let data = sectiondata.servicev3

    return <div>
        <section className="services-3-area services-page">
          <div className="container">
            <div className="row justify-content-start">
              <div className="col-lg-8">
                <div className="section-title">
                  <span>{ data.title }</span>
                  <h3 className="title">{ data.subtitle }</h3>
                </div>
              </div>
              <div className="col-lg-12">
                <div className="section-title text-center">
                  <ul className="nav nav-pills" id="pills-tab" role="tablist">
                    <li className="nav-item">
                      <a className="nav-link active" id="pills-profile-tab" data-toggle="pill" href="#pills-profile" role="tab" aria-controls="pills-profile" aria-selected="true">Software Help</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" id="pills-home-tab" data-toggle="pill" href="#pills-home" role="tab" aria-controls="pills-home" aria-selected="false">Hardware Help</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" id="pills-contact-tab" data-toggle="pill" href="#pills-contact" role="tab" aria-controls="pills-contact" aria-selected="false">Network Help</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" id="pills-car-tab" data-toggle="pill" href="#pills-car" role="tab" aria-controls="pills-car" aria-selected="false">Cloud Help</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <div className="services-3-box pt-50">
          <div className="container">
            <div className="tab-content" id="pills-tabContent">
              <div className="tab-pane fade" id="pills-home" role="tabpanel" aria-labelledby="pills-home-tab">
                <div className="row justify-content-center">

                  { data.items.map( ( item, i ) =>
                    <div  key={ i } className="col-lg-4 col-md-7 col-sm-9">
                      <div className="services-item mt-30">
                        <div className="services-thumb">
                          <img src={ publicUrl+item.image } alt={ imagealt } />
                        </div>
                        <div className="services-content">
                          <div className="services-title">
                             <img src={ publicUrl+item.icon } alt={ imagealt } />
                            <Link to={ item.url }><h5 className="title">{ item.title }</h5></Link>
                          </div>
                          <div className="text-center">
                            <p>{ item.content }</p>
                          </div>
                        </div>
                      </div>
                    </div>
                   ) }

                </div>
              </div>
              <div className="tab-pane fade  show active" id="pills-profile" role="tabpanel" aria-labelledby="pills-profile-tab">
                <div className="row justify-content-center">
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-6.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-1.png" alt="services" />
                          <Link to="/best-software-support-services"><h5 className="title">Operating System</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>The most well-known illustration of system software is this.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-7.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-2.png" alt="services" />
                          <Link to="/best-software-support-services"><h5 className="title">iOS App Maintenance Services</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>Are your iOS apps up-to-date, active, and error-free? With our iOS app maintenance services</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-8.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-3.png" alt="services" />
                          <Link to="/best-software-support-services"><h5 className="title">Web Maintenance Services</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>Updating websites, performing backups, evaluating page speeds, reviewing security updates</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="tab-pane fade" id="pills-contact" role="tabpanel" aria-labelledby="pills-contact-tab">
                <div className="row justify-content-center">

                <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-6.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-1.png" alt="services" />
                          <Link to="/best-network-support-services"><h5 className="title">Network Installation</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>Networks are essential to businesses' day-to-day operations, therefore they must be reliable and secure.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-7.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-2.png" alt="services" />
                          <Link to="/best-network-support-services"><h5 className="title">Commercial Wi-fi Installation</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>A fast and dependable Wi-Fi connection is essential for today's corporate operations.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-8.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-3.png" alt="services" />
                          <Link to="/best-network-support-services"><h5 className="title">Local Area Network</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>LANs are the most commonly discussed networks, one of the most prevalent, one of the most inventive.</p>
                        </div>
                      </div>
                    </div>
                  </div>

                </div>
              </div>
              <div className="tab-pane fade" id="pills-car" role="tabpanel" aria-labelledby="pills-car-tab">
                <div className="row justify-content-center">

                <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-6.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-1.png" alt="services" />
                          <Link to="/best-cloud-services"><h5 className="title">AWS Services</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>AWS provides a vast array of services. Virtual Private Cloud, EC2, AWS Data Transfer,etc.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-7.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-2.png" alt="services" />
                          <Link to="/best-cloud-services"><h5 className="title">Google Cloud</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>Google's cloud is the Google Cloud Platform. Google Cloud provides comparable services in a number of categories.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-7 col-sm-9">
                    <div className="services-item mt-30">
                      <div className="services-thumb">
                        <img src="assets/images/services-8.jpg" alt="services" />
                      </div>
                      <div className="services-content">
                        <div className="services-title">
                          <img src="assets/images/icon/icon-3.png" alt="services" />
                          <Link to="/best-cloud-services"><h5 className="title">Microsoft Azure</h5></Link>
                        </div>
                        <div className="text-center">
                          <p>Azure stands out as the most appealing and intelligent cloud platform because it is the only one to offer prior Microsoft products and services.</p>
                        </div>
                      </div>
                    </div>
                  </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

        }
}

export default ServicesV6