import React, { Component } from 'react';
import sectiondata from '../../data/sections.json';
import parse from 'html-react-parser';

class Portfolio extends Component {

    render() {

        let publicUrl = process.env.PUBLIC_URL+'/'
        let imagealt = 'image'
        let data = sectiondata.portfolio

    return <section className="portfolio-area pb-120">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <div className="section-title section-title-2 text-center">
                <span>{ data.title }</span>
                <h3 className="title">{ data.subtitle } </h3>
              </div>
            </div>
          </div>
          <div className="row justify-content-center">
            { data.items.map( ( item,i  )=>
                <div key={ i } className="col-lg-4 col-md-6 col-sm-8">
                  <div className="portfolio-item mt-30 wow fadeIn animated" data-wow-duration="2000ms" data-wow-delay={ item.delay }>
                    <img src={ publicUrl+item.image } alt={ imagealt } />
                    <div className="portfolio-content">
                      <span>{ item.title }</span>
                      <h4 className="title">{ item.subtitle }</h4>
                    </div>
                  </div>
                </div>
             ) }
          
          </div>
        </div>
      </section>

        }
}

export default Portfolio